import Vue from 'vue'

import MessageBox from './message-box.vue'
import Notifier from './notifier.vue'

import { isZh } from '../../utils/language'

const alertTitle = isZh ? 'æç¤º' : 'Alert'
const errorTitle = isZh ? 'éè¯¯' : 'Error'
const confirmTitle = isZh ? 'ç¡®è®¤æç¤º' : 'Confirm'
const warnTitle = isZh ? 'ç¡®è®¤è­¦å' : 'Warning'

const defaultButtons = [
  {
    id: 'ok',
    caption: isZh ? 'ç¡®å®' : 'OK',
    action: 'close'
  },
  {
    id: 'cancel',
    caption: isZh ? 'åæ¶' : 'CANCEL',
    action: 'close'
  },
  {
    id: 'yes',
    caption: isZh ? 'æ¯' : 'YES',
    action: 'close'
  },
  {
    id: 'no',
    caption: isZh ? 'å¦' : 'NO',
    action: 'close'
  }
]

export function showMessage (options) {
  const { title, message, buttons, primaryButton, danger, callback } = options
  const dialog = new Vue({
    extends: MessageBox,
    data: {
      message
    },
    title,
    danger,
    buttons: buttons.map(btn =>
      (defaultButtons.find(item => item.id === btn) || btn)
    ),
    primaryButton: primaryButton === undefined
      ? buttons[0]
      : primaryButton
  })
  if (callback) dialog.$on('hide', callback)
  dialog.show()
}

export function alert (message, callback) {
  showMessage({
    title: alertTitle,
    buttons: ['ok'],
    message,
    callback
  })
}

export function error (message, callback) {
  showMessage({
    title: errorTitle,
    buttons: ['ok'],
    danger: true,
    message,
    callback
  })
}

export function confirm (message, callback) {
  showMessage({
    title: confirmTitle,
    buttons: ['ok', 'cancel'],
    message,
    callback
  })
}

export function warn (message, callback) {
  showMessage({
    title: warnTitle,
    buttons: ['ok', 'cancel'],
    danger: true,
    message,
    callback
  })
}

let notifier

export function notify (notifyType, message, timeout) {
  if (!notifier) {
    notifier = new Vue(Notifier).$mount()
    document.body.appendChild(notifier.$el)
  }
  notifier.notify(notifyType, message, timeout)
}
